import skeleton from "../../components/quick-skeleton/quick-skeleton.vue";
import {
		mapState,
		mapMutations
} from 'vuex';

	export default {
		data() {
			return {
				showSkeleton: true,  //骨架屏显示隐藏
				sizeCalcState: false,
				tabScrollTop: 0,
				currentId: '1',
				flist: [
					{
						title: "女装",
						id: '1'
					},
					{
						title: "女装",
						id: '2'
					},
					{
						title: "女装",
						id: '3'
					},
					{
						title: "女装",
						id: '4'
					},
					{
						title: "女装",
						id: '5'
					},
				],
				slist: [
					{
						title: "女装",
						id: '6'
					}
				],
				tlist: [
					{
						img: "https://img.alicdn.com/imgextra/i1/2053469401/O1CN01Kf85St2JJhutCPY0J-2053469401.png",
						title: "外套",
						id: '7',
						pid: '6'
					},
					{
						img: "https://img.alicdn.com/imgextra/i1/2053469401/O1CN01Kf85St2JJhutCPY0J-2053469401.png",
						title: "外套",
						id: '8',
						pid: '6'
					},
					{
						img: "https://img.alicdn.com/imgextra/i1/2053469401/O1CN01Kf85St2JJhutCPY0J-2053469401.png",
						title: "外套",
						id: '9',
						pid: '6'
					},
					{
						img: "https://img.alicdn.com/imgextra/i1/2053469401/O1CN01Kf85St2JJhutCPY0J-2053469401.png",
						title: "外套",
						id: '10',
						pid: '6'
					},
					{
						img: "https://img.alicdn.com/imgextra/i1/2053469401/O1CN01Kf85St2JJhutCPY0J-2053469401.png",
						title: "外套",
						id: '11',
						pid: '6'
					},
					{
						img: "https://img.alicdn.com/imgextra/i1/2053469401/O1CN01Kf85St2JJhutCPY0J-2053469401.png",
						title: "外套",
						id: '12',
						pid: '6'
					}
				],
			}
		},
		components: {
			skeleton
		},
		computed: {
			...mapState(['cateId','hasCateId'])
		},
		onReady(){
			const that = this;
			that.$refs.skeleton.attachedAction();
			that.$refs.skeleton.readyAction();
			that.sendRequest();
		},
		onShow () {
			const that = this;
			if (that.showSkeleton === false) {
				if(this.hasCateId){
					setTimeout(() => {
						this.tabtap(this.cateId)
						this.clearCate();
					}, 100);
				}
			}
		},
		methods: {
			...mapMutations(['clearCate']),
			async sendRequest(){
				this.flist = [];
				this.slist = [];
				this.tlist = [];
				let list = await this.$request.post('/taobao-buy/menu');
				list.data.data.forEach(item => {
					if(item.pid == '0'){
						this.flist.push(item);  // 一级分类
						this.slist.push(item)
					}
				})
				for (const item of this.flist) {
					this.tlist.push(...item.son); // 二级分类
				}

				this.callBack();
			},
			callBack(){
				this.showSkeleton = false;
				if(this.hasCateId){
					setTimeout(() => {
						this.tabtap(this.cateId)
					}, 1000);
				}
			},
			//一级分类点击
			tabtap(item){
				if(!this.sizeCalcState){
					this.calcSize();
				}

				this.currentId = item.id;
				console.log(this.slist.findIndex)
				let index = this.slist.findIndex(sitem=>sitem.id === item.id);
				this.tabScrollTop = this.slist[index].top;
			},
			//右侧栏滚动
			asideScroll(e){
				if(!this.sizeCalcState){
					this.calcSize();
				}
				let scrollTop = e.detail.scrollTop + 200;
				let tabs = this.slist.filter(item=>item.top <= scrollTop).reverse();
				if(tabs.length > 0){
					this.currentId = tabs[0].id;
				}
			},
			//计算右侧栏每个tab的高度等信息
			calcSize(){
				let h = 0;
				this.slist.forEach(item=>{
					let view = uni.createSelectorQuery().select("#main-" + item.id);
					view.fields({
						size: true
					}, data => {
						item.top = h;
						h += data.height;
						item.bottom = h;
					}).exec();
				})
				this.sizeCalcState = true;
			},
			navToList(key){
				uni.navigateTo({
					url: `/pages/product/goods-list?key=${key}`
				})
			},
			navToCate () {

			}
		}
	}
